'use client';

import React, { useCallback } from 'react';
import { Save, RotateCcw } from 'lucide-react';
import { SETTINGS_PAGE_STYLES } from '../constants/styles';

/**
 * üî• Ïï°ÏÖò Î≤ÑÌäº Props
 */
interface SettingsActionsProps {
  saving: boolean;
  onSave: () => Promise<void>;
  onReset: () => Promise<void>;
}

/**
 * üî• ÏÑ§Ï†ï Ïï°ÏÖò Î≤ÑÌäº Ïª¥Ìè¨ÎÑåÌä∏
 */
export const SettingsActions = React.memo<SettingsActionsProps>(({ 
  saving, 
  onSave, 
  onReset 
}) => {
  // üî• Ï†ÄÏû• Ìï∏Îì§Îü¨
  const handleSave = useCallback(async () => {
    try {
      await onSave();
    } catch (error) {
      console.error('Failed to save settings:', error);
    }
  }, [onSave]);

  // üî• Î¶¨ÏÖã Ìï∏Îì§Îü¨ (ÌôïÏù∏ Îã§Ïù¥ÏñºÎ°úÍ∑∏ Ìè¨Ìï®)
  const handleReset = useCallback(async () => {
    const confirmed = window.confirm(
      'Î™®Îì† ÏÑ§Ï†ïÏùÑ Í∏∞Î≥∏Í∞íÏúºÎ°ú Ï¥àÍ∏∞ÌôîÌïòÏãúÍ≤†ÏäµÎãàÍπå?\nÏù¥ ÏûëÏóÖÏùÄ ÎêòÎèåÎ¶¥ Ïàò ÏóÜÏäµÎãàÎã§.'
    );
    
    if (confirmed) {
      try {
        await onReset();
      } catch (error) {
        console.error('Failed to reset settings:', error);
      }
    }
  }, [onReset]);

  return (
    <div className={SETTINGS_PAGE_STYLES.actions}>
      <button
        type="button"
        className={`${SETTINGS_PAGE_STYLES.button} ${SETTINGS_PAGE_STYLES.secondaryButton}`}
        onClick={handleReset}
        disabled={saving}
      >
        <RotateCcw className="w-4 h-4 mr-2" />
        Í∏∞Î≥∏Í∞íÏúºÎ°ú Î≥µÏõê
      </button>
      
      <button
        type="button"
        className={`${SETTINGS_PAGE_STYLES.button} ${SETTINGS_PAGE_STYLES.primaryButton}`}
        onClick={handleSave}
        disabled={saving}
      >
        <Save className="w-4 h-4 mr-2" />
        {saving ? 'Ï†ÄÏû• Ï§ë...' : 'Î™®Îì† ÏÑ§Ï†ï Ï†ÄÏû•'}
      </button>
    </div>
  );
});

SettingsActions.displayName = 'SettingsActions';
