'use client';

import { useState, useEffect, useCallback, useRef } from 'react';
import { Logger } from '../../../../shared/logger';
import type { SettingsData, UseSettingsReturn } from '../types';

// üî• Í∏∞Í∞ÄÏ∞®Îìú useSettings ÌõÖ - Electron-Store ÏôÑÎ≤Ω Ìò∏Ìôò!

/**
 * üî• Settings Í¥ÄÎ¶¨Î•º ÏúÑÌïú Ïª§Ïä§ÌÖÄ ÌõÖ
 * - main/settings/ElectronStoreSettingsManagerÏôÄ ÏôÑÎ≤Ω Ìò∏Ìôò
 * - IPC ÌÜµÏã†ÏùÑ ÌÜµÌïú ÏÑ§Ï†ï Í¥ÄÎ¶¨
 * - ÌÉÄÏûÖ ÏïàÏ†ÑÌïú ÏÑ§Ï†ï ÏóÖÎç∞Ïù¥Ìä∏
 * - ÏÑ±Îä• ÏµúÏ†ÅÌôîÎêú ÏÉÅÌÉú Í¥ÄÎ¶¨
 */
export function useSettings(): UseSettingsReturn {
  const [settings, setSettings] = useState<SettingsData | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [saving, setSaving] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  
  // üî• Ï§ëÎ≥µ ÏöîÏ≤≠ Î∞©ÏßÄÎ•º ÏúÑÌïú ref
  const loadingRef = useRef<boolean>(false);
  const savingRef = useRef<boolean>(false);

  /**
   * üî• Í∏∞Î≥∏Í∞í Ï†ïÏùò (main/settingsÏôÄ ÎèôÏùº)
   */
  const defaultSettings: SettingsData = {
    app: {
      theme: 'system',
      language: 'ko',
      autoSave: true,
      startMinimized: false,
      minimizeToTray: true,
      fontSize: 14,
      fontFamily: 'Pretendard, -apple-system, BlinkMacSystemFont, system-ui, sans-serif',
    },
    keyboard: {
      enabled: true,
      language: 'korean',
      trackAllApps: false,
      sessionTimeout: 30,
    },
    ui: {
      windowWidth: 1400,
      windowHeight: 900,
      sidebarCollapsed: false,
      focusMode: false,
      showLineNumbers: true,
      showWordCount: true,
    },
    performance: {
      enableGPUAcceleration: true,
      maxCPUUsage: 80,
      maxMemoryUsage: 2048,
      enableHardwareAcceleration: true,
    },
  };

  /**
   * üî• ÏÑ§Ï†ï Î°úÎìú (Ï§ëÎ≥µ Î∞©ÏßÄ)
   */
  const loadSettings = useCallback(async (): Promise<void> => {
    if (loadingRef.current) return;
    
    try {
      loadingRef.current = true;
      setLoading(true);
      setError(null);
      
      Logger.debug('USE_SETTINGS', 'Loading settings from main process');
      
      const result = await window.electronAPI.settings.get('all');
      
      if (result.success && result.data) {
        setSettings(result.data as SettingsData);
        Logger.info('USE_SETTINGS', 'Settings loaded successfully', result.data);
      } else {
        Logger.warn('USE_SETTINGS', 'Failed to load settings, using defaults', result.error);
        setSettings(defaultSettings);
        setError('Í∏∞Î≥∏ ÏÑ§Ï†ïÏùÑ ÏÇ¨Ïö©Ìï©ÎãàÎã§.');
      }
    } catch (error) {
      Logger.error('USE_SETTINGS', 'Error loading settings', error);
      setSettings(defaultSettings);
      setError(error instanceof Error ? error.message : 'ÏÑ§Ï†ï Î°úÎìú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
    } finally {
      setLoading(false);
      loadingRef.current = false;
    }
  }, []);

  /**
   * üî• ÏÑ§Ï†ï ÏóÖÎç∞Ïù¥Ìä∏ (ÌÉÄÏûÖ ÏïàÏ†Ñ)
   */
  const updateSetting = useCallback(async <K extends keyof SettingsData, T extends keyof SettingsData[K]>(
    category: K,
    key: T,
    value: SettingsData[K][T]
  ): Promise<void> => {
    if (!settings || savingRef.current) return;

    try {
      setSaving(true);
      
      // üî• Ï¶âÏãú UI ÏóÖÎç∞Ïù¥Ìä∏ (ÎÇôÍ¥ÄÏ†Å ÏóÖÎç∞Ïù¥Ìä∏)
      setSettings(prev => {
        if (!prev) return null;
        
        return {
          ...prev,
          [category]: {
            ...prev[category],
            [key]: value,
          },
        };
      });

      // üî• Î∞±ÏóîÎìúÏóê Ï†ÄÏû• (dot notation ÏÇ¨Ïö©)
      const keyPath = `${category}.${String(key)}`;
      Logger.debug('USE_SETTINGS', `Updating setting: ${keyPath}`, { value });
      
      const result = await window.electronAPI.settings.set(keyPath, value);
      
      if (result.success) {
        Logger.info('USE_SETTINGS', `Setting updated successfully: ${keyPath}`, { value });
      } else {
        throw new Error(result.error || `Failed to save ${keyPath}`);
      }
    } catch (error) {
      Logger.error('USE_SETTINGS', `Error updating setting: ${category}.${String(key)}`, error);
      
      // üî• ÏóêÎü¨ Ïãú Î°§Î∞±
      await loadSettings();
      setError(`ÏÑ§Ï†ï Ï†ÄÏû• Ïã§Ìå®: ${error instanceof Error ? error.message : 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'}`);
    } finally {
      setSaving(false);
    }
  }, [settings, loadSettings]);

  /**
   * üî• Î™®Îì† ÏÑ§Ï†ï Ï†ÄÏû•
   */
  const saveAllSettings = useCallback(async (): Promise<void> => {
    if (!settings || savingRef.current) return;

    try {
      savingRef.current = true;
      setSaving(true);
      setError(null);
      
      Logger.info('USE_SETTINGS', 'Saving all settings...');
      
      // üî• Í∞Å Ïπ¥ÌÖåÍ≥†Î¶¨Î≥ÑÎ°ú Ï†ÄÏû•
      for (const [category, categoryData] of Object.entries(settings)) {
        const result = await window.electronAPI.settings.set(category, categoryData);
        if (!result.success) {
          throw new Error(`Failed to save ${category} settings: ${result.error}`);
        }
      }
      
      Logger.info('USE_SETTINGS', 'All settings saved successfully');
    } catch (error) {
      Logger.error('USE_SETTINGS', 'Failed to save all settings', error);
      setError(`Ï†ÑÏ≤¥ ÏÑ§Ï†ï Ï†ÄÏû• Ïã§Ìå®: ${error instanceof Error ? error.message : 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'}`);
    } finally {
      setSaving(false);
      savingRef.current = false;
    }
  }, [settings]);

  /**
   * üî• ÏÑ§Ï†ï Î¶¨ÏÖã
   */
  const resetSettings = useCallback(async (): Promise<void> => {
    if (savingRef.current) return;

    try {
      savingRef.current = true;
      setSaving(true);
      setError(null);
      
      Logger.info('USE_SETTINGS', 'Resetting all settings...');
      
      const result = await window.electronAPI.settings.reset();
      
      if (result.success) {
        // üî• ÏÑ±Í≥µ Ïãú Í∏∞Î≥∏Í∞íÏúºÎ°ú ÏÑ§Ï†ïÌïòÍ≥† Îã§Ïãú Î°úÎìú
        setSettings(defaultSettings);
        await loadSettings();
        Logger.info('USE_SETTINGS', 'Settings reset successfully');
      } else {
        throw new Error(result.error || 'Failed to reset settings');
      }
    } catch (error) {
      Logger.error('USE_SETTINGS', 'Failed to reset settings', error);
      setError(`ÏÑ§Ï†ï Ï¥àÍ∏∞Ìôî Ïã§Ìå®: ${error instanceof Error ? error.message : 'Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò'}`);
    } finally {
      setSaving(false);
      savingRef.current = false;
    }
  }, [loadSettings]);

  /**
   * üî• Ï¥àÍ∏∞ ÏÑ§Ï†ï Î°úÎìú
   */
  useEffect(() => {
    loadSettings();
  }, [loadSettings]);

  /**
   * üî• ÏóêÎü¨ ÏûêÎèô ÌÅ¥Î¶¨Ïñ¥ (10Ï¥à ÌõÑ)
   */
  useEffect(() => {
    if (error) {
      const timer = setTimeout(() => {
        setError(null);
      }, 10000);
      
      return () => clearTimeout(timer);
    }
  }, [error]);

  return {
    settings,
    loading,
    saving,
    error,
    updateSetting,
    saveAllSettings,
    resetSettings,
    setSettings,
  };
}
